import React, { useRef } from 'react';
import { useForm } from "react-hook-form";
import emailjs from '@emailjs/browser';
import styles from './Form.module.css'

function Form(props) {
	const form = useRef();
	
	const {
		register,
		formState: {errors, isValid},
		reset,
	} = useForm({
		mode: 'onBlur',
	});
	const sendEmail = (e) => {
		e.preventDefault(); // prevents the page from reloading when you hit Send
		
		emailjs.sendForm('service_s8rt1yt', 'template_k49zeit', form.current, 'MMSRppZb4YlpmEjqY')
		.then((result) => {
			alert('   !');
		}, (error) => {
			alert(' -  ,   !');
		});
		reset();
	};
	
	return (
		<form ref={form} onSubmit={sendEmail} className={styles.form}>
			<label>Your First Name:
				<input
					{...register('firstName', {
						required: '   !',
						minLength: {
							value: 2,
							message: '      !',
						},
						maxLength: {
							value: 10,
							message: '   ',
						},
				})} type="text"/>
			</label>
			<span className={styles.error}>
				{errors?.firstName && <p>{errors?.firstName?.message || 'Error!'}</p>}
			</span>
			<label>Your Last Name:
				<input
					{...register('lastName', {
						required: '   !',
						minLength: {
							value: 2,
							message: '      !',
						},
						maxLength: {
							value: 10,
							message: '   \'',
						},
					})} type="text"/>
			</label>
			<span className={styles.error}>
				{errors?.lastName && <p>{errors?.lastName?.message || 'Error!'}</p>}
			</span>
			<label>Your Email: name@gmail.com
				<input
					{...register('email', {
						required: '   !',
						pattern: {
							value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
							message: "    !"
						}
					})} type="text"/>
			</label>
			<span className={styles.error}>
				{errors?.email && <p>{errors?.email?.message || 'Error!'}</p>}
			</span>
			<label>Your Telefon: 380676742376
				<input
					{...register('telefon', {
						required: '   !',
						minLength: {
							value: 12,
							message: '   12    ',
						},
						maxLength: {
							value: 12,
							message: '   12    ',
						},
					})} type="number"/>
			</label>
			<span className={styles.error}>
				{errors?.telefon && <p>{errors?.telefon?.message || 'Error!'}</p>}
			</span>
			<label>Your message:
				<textarea
					{...register('textMessage', {
						required: '   !',
						minLength: {
							value: 10,
							message: '     10 !',
						},
						maxLength: {
							value: 3000,
							message: '   ',
						},
					})}/>
			</label>
			<span className={styles.error}>
				{errors?.textMessage && <p>{errors?.textMessage?.message || 'Error!'}</p>}
			</span>
			<input type="submit" disabled={!isValid} value={props.text}/>
		</form>
	);
}

Form.defaultProps = {
	text: '',
	
};

export default Form;